<template>
  <img src="../assets/404.webp" alt="" />

  <el-alert
    id="tips"
    title="警告！"
    type="error"
    :closable="false"
    show-icon
    center
    description="你来到此页面是因为访问的路径不存在，或无权访问，将在 {{time}} 秒后强制退出后回到登录页!"
  >
    你来到此页面是因为访问的路径不存在，或无权访问，将在 &nbsp; {{
      time
    }} &nbsp; 秒后强制退出后回到登录页!
  </el-alert>
</template>

<script>
import { onMounted, reactive, toRefs } from "@vue/runtime-core";
import router from "@/router";
export default {
  setup() {
    const state = reactive({
      time: 5,
    })

    onMounted(() => {
      // alert('你来到此页面是因为访问的路径不存在，或无权访问，将在强制退出后回到登录页!')
      setInterval(function () {
        state.time = state.time -1
        if(state.time == 0){
          router.push("/");
  
        }
         
      }, 1000)

    })

    return {
      ...toRefs(state),
    };
  },
};
</script>


<style scoped>
#tips {
  width: 50%;
  margin: 0 auto;
}

.el-alert {
  margin: 20px 0 0;
}
.el-alert:first-child {
  margin: 0;
}
</style>





